import React,{useEffect,useCallback} from 'react'
import {Table,Popconfirm,Button} from "antd"
import {ClearOutlined } from "@ant-design/icons";
import type { ColumnsType } from "antd/es/table";
import { useDispatch,useSelector} from "react-redux"
import {asyncInitList as initList,asyncDelList as delList, initPayload} from "../store/infoReducer"

interface DataType {
  _id:string,
  orderid: String; //订单编号
  partaid: {partaname:string};
  partbid: string;
  productname: String; //产品名称
  productmodel: String; //产品型号
  productcolor: String; //颜色 后期变成type 常量设置的的
  productlight: String; //光源
  productdirection: String; //方向
  productsize: Number; //规格(mm)
  productnum: Number; //数量
  productprice: Number; //产品价格,
  date: Date;
}

export default function OrderList() {
  let dispatch = useDispatch()
  let list = useSelector((store:any)=>store.info.orderList)
  let getList = useCallback(()=>{
    dispatch(initList(initPayload.orderList) as any)
  },[dispatch])
  useEffect(()=>{
    getList()
  },[])
  const columns: ColumnsType<DataType> = [
    {
      title: "订单编号",
      dataIndex:"orderid"
    },
    {
      title: "甲方名称",
      dataIndex:"partaid",
      render:(_,record)=>{
        let partaname= record.partaid?.partaname
        return (
          <>{partaname}</>
        )
      }
    },
    {
      title: "产品名称",
      dataIndex: "productname",
    },
    {
      title: "产品型号",
      dataIndex: "productmodel",
    },
    {
      title: "表面处理(颜色)",
      dataIndex: "productcolor",
    },
    {
      title: "光源",
      dataIndex: "productlight",
    },
    {
      title: "出线方向",
      dataIndex: "productdirection",
    },
    {
      title: "规格(mm)",
      dataIndex: "productsize",
    },
    {
      title: "数量(支)",
      dataIndex: "productnum",
    },
    {
      title: "单价(元/米)",
      dataIndex: "productprice",
    },
    {
      title: "金额(元)",
      // dataIndex: "productprice",
      render:(_,record)=>{
        return <>{(Number(record.productprice)*Number(record.productnum)*Number(record.productsize)/1000).toFixed(2)}</>
      }
    },
    {
      title: "状态",
      render:(_,record)=>{
        return <Popconfirm
        title="你确定要删除吗?"
        onConfirm={() => {
          delItem(record._id);
        }}
        onCancel={()=>{}}
        okText="确定"
        cancelText="取消"
      >
        <Button
          type="primary"
          style={{ backgroundColor: "#e2a241", border: "#edc523" }}
          icon={<ClearOutlined />}
        >
          删除
        </Button>
      </Popconfirm>
      }
    },
  ];
  let delItem=(_id:any)=>{
    dispatch(delList(_id,initPayload.orderList) as any)
  }
  return (
    <Table
        style={{maxHeight:"100%",overflowY:"auto"}}
        columns={columns}
        dataSource={list}
        scroll={{ x: 1024}}
        rowKey="_id"
        bordered
        pagination={false}
        
      />
  )
}
